.details {
  @extend %flexCenter;
  flex-direction: column;
  position: relative;
  a {
    position: absolute;
    top: 0;
    left: 0;
    text-decoration: underline;
    &:hover {
      color: #808080;
    }
  }

  &__img {
    margin-top: 1em;
    height: 250px;
    width: 100%;
    object-fit: cover;
  }

  &__title {
    margin: 0.5em 0 2em;
    font-size: 2em;
    width: 100%;
    text-align: center;
    word-break: break-word;
  }
  &__text {
    width: 100%;
    margin-bottom: 2em;
    resize: none;
    padding: 0.5em;
    word-break: break-word;
    min-height: 250px;
  }

  &__footer {
    display: flex;
    width: 100%;
    justify-content: space-between;
    margin-bottom: 2em;
    align-items: end;
  }
}
