.form {
  display: flex;
  flex-direction: column;
  padding: 3em 0;

  .form__label {
    margin: 15px 0 5px;
  }

  .form__input {
    border: 1px solid #dcdcdc;
    border-radius: 3px;
    &:focus {
      outline: 1px solid #aaaaaa;
      background-color: #f3f3f3;
    }
    &--textarea {
      resize: none;
      height: 250px;
    }
  }

  .form__btn {
    width: fit-content;
    margin-top: 15px;
    background-color: #dcdcdc;
    padding: 5px 10px;
    border-radius: 3px;
  }
}
