@import "./_variables";
@import "./_placeholders";
@import "../components/Header/Header.scss";
@import "../screens/Articles/ArticlesScreen.scss";
@import "../screens/Form/FormScreen.scss";
@import "../screens/Details/Details.scss";
@import "../components/Buttons/Buttons.scss";
@import "../components/IdentificationModals/Identification.scss";

html,
body,
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen,
    Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;

  font-size: clamp(14px, 1.5vmax, 20px);
}

a {
  color: inherit;
  text-decoration: none;
}

ul {
  list-style: none;
}

button {
  cursor: pointer;
  border: none;
  background-color: transparent;
}

img {
  width: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.container {
  min-width: 300px;
  max-width: 1000px;
  margin-left: auto;
  margin-right: auto;
}
