.btns {
  padding: 0.5em 1em;
  color: white;

  button {
    padding: 5px 10px;
    border-radius: 5px;
    color: #ffffff;
    margin-left: 1em;
  }

  &--delete {
    background-color: #690101;
  }

  &--update {
    background-color: #000146;
  }

  &--cancel {
    background-color: #000000;
  }

  &--send {
    background-color: #00460f;
  }
}
