.registration {
  position: absolute;
  inset: 0;

  &__overlay {
    position: absolute;
    inset: 0;
    background-color: rgba(#000000, 0.8);
    z-index: 10;
  }

  &__form {
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: #ffffff;
    min-width: 300px;
    width: 25%;
    min-height: 300px;
    padding: 20px 15px;
    z-index: 100;

    .registration__close {
      position: absolute;
      top: 0;
      right: 0;
      padding: 5px 10px;
    }

    .registration__alert {
      color: #990202;
    }

    .registration__switch {
      font-size: 0.8em;

      span {
        font-size: 1em;
        text-decoration: underline;
        cursor: pointer;
        &:hover {
          color: gray;
        }
      }
    }
  }

  &__btn {
    background-color: black;
    color: white;
    padding: 5px 0;
    margin-top: 10px;
  }
}
