.navigation {
  position: relative;
  padding: 30px 0;

  &__list {
    @extend %flexCenter;
    margin-top: 10px;

    .navigation__item {
      margin: 0 2em;
      color: #c2c2c2;

      .active {
        color: black;
        border-bottom: 2px solid black;
      }
    }
  }

  &__identification {
    display: flex;
    align-items: center;
    position: absolute;
    top: 5px;
    right: 0;
  }
  &__btn {
    padding: 5px 10px;
    border-radius: 5px;
    margin-right: 5px;
    margin-left: 10px;

    &--black {
      background-color: black;
      color: white;
    }
    &--red {
      background-color: rgb(177, 2, 2);
      color: white;
    }
  }
}
