.articles {
  display: grid;
  grid-template-columns: repeat(auto-fill, 300px);
  grid-auto-rows: 370px;
  padding: 3em 0;
  grid-gap: 2em;

  justify-content: center;

  &__card {
    position: relative;
    box-shadow: 0 0 5px gainsboro;
    transition: box-shadow 0.2s;
    height: 400px;
    max-height: 400px;

    &:hover {
      box-shadow: 0 0 5px rgb(112, 112, 112);
    }
    .articles__img {
      height: 200px;
      object-fit: cover;
    }

    .articles__content {
      padding: 5px 15px;

      .articles__title {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }

      .articles__text {
        height: 140px;
        overflow: hidden;
      }

      .articles__author,
      .articles__time {
        position: absolute;
        bottom: 5px;
        right: 15px;
        font-size: 0.8em;
        color: rgb(128, 128, 128);
      }
      .articles__author {
        left: 15px;
      }
    }
  }
}
